import './RandomRecipe.css'
import { fujiRecipes } from  './fujixweeklyrecipes.js'

function RandomRecipe(props) {

	let recipes = props.sensor == "xTransIII" ? fujiRecipes.xTransIII : fujiRecipes.xTransIV;
	let randomNumber = Math.floor(Math.random() * recipes.length);
	let recipe = recipes[randomNumber];

	return (
		<div className="RandomRecipe-div">
            Your random recipe for {props.sensor} sensor is ... <br />
            <a className="RandomRecipe-link" href={recipe.url}
            >
            {recipe.name}
            </a>
        </div>
    );
}

export default RandomRecipe;